import React from 'react';
import { Route, Routes } from 'react-router-dom';
import DashboardLayout from '../../layout/Dashboard';

const TransactionsList = React.lazy(() => import('./TransactionList/Index'));
const TransactionFilter = React.lazy(() => import('./TransactionFilter/Index'));

function TransactionRouter() {
  return (
    <Routes>
      <Route
        path=""
        element={(
          <DashboardLayout>
            <TransactionsList />
          </DashboardLayout>
)}
      />
      <Route path="filter" element={<TransactionFilter />} />
    </Routes>
  );
}

export default TransactionRouter;
